import React from 'react'
import { ConnectedRouter } from 'connected-react-router'
import { history, /* history2 */ } from './store'
import Init from '../myApp/pages/Init'
import WebviewLoader from '../myApp/pages/WebviewLoader'
import ItemDetails from '../myApp/pages/ItemDetails'
import Dashboard from '../myApp/pages/Dashboard'
import EsSlider from '../myApp/pages/EsSlider'
import MySkills from '../myApp/pages/MySkills'
import * as types from '../myApp/actions/types'
/* -------------- */
import { Route, Switch, MemoryRouter } from 'react-router-native' /* note:we used alias for web (webpack.config.js react-router-dom)*/
/* use this if you just update your env and get lots of error:
  const Test =()=>{
  <View><Text>ok</Text></View>
} */
/* ========================================================================== */
export const routes_ = {
  '': 'Init',
  'Init': 'Init',
  'ItemDetails': 'ItemDetails',
  'WebviewLoader': 'WebviewLoader',
  'Dashboard': 'Dashboard',
  'EsSlider': 'EsSlider',
  'MySkills': 'MySkills',
}
/* ========================================================================== */
type State = {}
type Props = {}
class AzRouter extends React.Component<Props, State> {
  render() { 
    return (
      <ConnectedRouter history={history}>
        {/*   <Router history={history2}> */}
        <MemoryRouter>
          <Switch>
            <Route exact path="/" component={Init} />
            <Route exact path={'/' + routes_.Init} component={Init} />
            <Route exact path={'/' + routes_.ItemDetails} component={ItemDetails} />
            <Route exact path={'/' + routes_.WebviewLoader} component={WebviewLoader} />
            <Route exact path={'/' + routes_.Dashboard} component={Dashboard} />
            <Route exact path={'/' + routes_.EsSlider} component={EsSlider} />
            <Route exact path={'/' + routes_.MySkills} component={MySkills} />
          </Switch>
        </MemoryRouter>
        {/* </Router> */}
      </ConnectedRouter>
    )
    /* ----------------------- */
  }
}
/*================================================== */
export default AzRouter
