export async function createChartConfigAndSendIt(webview1Ref: EsWebView, confirmedUsersPerDay, confirmedSalesPerDay) {
  console.log({ confirmedSalesPerDay })
  // const data = {
  const chartConfigs: chartJs.ChartConfiguration = {
    type: 'line',
    data: {
      labels: confirmedUsersPerDay.map(i => i.x),
      datasets: [
        {
          label: 'marketers',
          backgroundColor: 'rgb(255, 99, 132)',
          borderColor: 'rgb(255, 99, 132)',
          data: confirmedUsersPerDay.map(i => i.y).reverse()
        },
      ]
    },
    options: {
      /* asdi28hsa */
      // height:200,
    }
  }
  const chartConfigs2: chartJs.ChartConfiguration = {
    type: 'line',
    data: {
      labels: confirmedSalesPerDay.map(i => i.x),
      datasets: [
        {
          label: 'sales',
          backgroundColor: 'rgb(55, 99, 32)',
          borderColor: 'rgb(55, 19, 12)',
          data: confirmedSalesPerDay.map(i => i.y).reverse()
        }
      ]
    },
    options: {
      /* asdi28hsa */
      // height:200,
    }
  }
  webview1Ref.sendMessageToWebView({
    event: 'rerenderWholeNetwork',
    content: [
      chartConfigs,
      chartConfigs2,
    ]
  })
}
/* ================================= */
export async function convertMarketerClassToVisNet(skills: { [key: string]: technologie }) {

  const nodes: visNetwork.Node[] = [
    // { id: 1, label: "you", level: 0, image: 'http://localhost:3000/images/logo.png' },
  ]
  const edges: visNetwork.Edge[] = []

  const node: visNetwork.Node = {
    id: 'aminZarei72',
    label: 'Amin Zarei',
    image: `/assets/images/aminZarei.png`,
    level: 0,
  }
  nodes.push(node)

  for (const id in skills) {
    // for (let index = 0; index < skills.length; index++) {
    const techno = skills[id];

    const node: visNetwork.Node = {
      id: id,
      label: techno.title,
      image: `/assets/images/technologies/${techno.icon}.png`,
      level: techno.level,
    }
    // node['image'] = imgPath

    nodes.push(node)
    if (techno.parent) {
      // edges.push({ from: id, to: techno.parent })
      edges.push({ from: techno.parent, to: id })
    } else {
      // edges.push({ from: id, to: 'aminZarei72' })
      edges.push({ from: 'aminZarei72', to: id })
    }
  }

  return { nodes, edges }
}

