export function resetScroll() {
  /* todo:we also need to disable current loading process(loadMore2) */
  // myApp.showSpinner({ content: '' })
  return new Promise(resolve => {
    //@ts-expect-error
    const _this = this as any
    _this.range = 0 /* reset range */
    // _this.loadMoreIndexr = 1
    // _this.scrollRef.eve
    _this.loadingMore = true /* disable scroll event(note:_this var has a different usage but since _this variable also disables scroll event we've used _this) */
    _this.setState({ loadingMore2: true, })
    // _this.scrollRef.scrollTo({ x: 0, y: 0, animated: false })
    try {
      _this.animXVal.stopAnimation(() => {
        _this.animXVal.setValue(0)
        _this.scrollRef.scrollToIndex({ animated: true, index: 0 })
      })
    } catch (error) {
      if ((error as any).name == 'Invariant Violation') {
        // console.log(error)
        console.log('Invariant Violation: scrollToIndex out of range: 0 vs -1')
      } else {
        console.error(error)
      }
    }
    setTimeout(() => {
      /* note:_this is necessary since scrollTo is not a async fn */
      _this.setState({ loadingMore2: false, })
      _this.loadingMore = false
      return resolve(true)
      // myApp.showSpinner(false)
    }, 100)
  })
}
