export function showPupop(args: (false | {
  duration?: number,
  title: string,
  content?: string,
  icon?: string,
  type?: 'alert' | 'confirm',
  callbackFn?: Function,
  dontCloseAfterUserClick?: boolean,
  withCheckBox?: {
    defaultValue: boolean,
    text: string,
  },

})) {
  // myApp.firebaseFns.get()
  if (!args) {
    gif.setStore({ popupDetail: { visible: false } })
    myApp.sharedVars.tmpPopupCallbackFn = () => { }
  } else {
    myApp.sharedVars.tmpPopupCallbackFn = args.callbackFn ? args.callbackFn : () => { }
    gif.setStore({
      popupDetail: {
        visible: true,
        content: args.content,
        title: args.title,
        icon: args.icon,
        type: args.type,
        withCheckBox: args.withCheckBox,
        /* #issue:asdq2322wfw223 */
        // callbackFn: (res) => { 
        //   if (!args.dontCloseAfterUserClick) gif.setStore({ popupDetail: { visible: false } })
        //   if (args.callbackFn) args.callbackFn(res)
        // }
      }
    })
  }
}
