export function showSpinner(args: (false | {
  content?: string,
  duration?: number,
  title?: string,
  type?: 'alert' | 'confirm',
  /** note:this wont work if u dont use the duration  */
  callbackFn?: Function,
  // dontCloseAfterUserClick?: boolean,

})) {

  if (!args) {
    gif.setStore({ spinnerDetails: { visible: false } })
  } else {
    // gif.setStore({ spinnerDetails: { visible: false } })
    gif.setStore({
      spinnerDetails: {
        visible: true,
        content: args.content,
        title: args.title,
        type: args.type,
        // callbackFn: (res) => {
        //   if (!args.dontCloseAfterUserClick) gif.setStore({ spinnerDetails: { visible: false } })
        //   if (args.callbackFn) args.callbackFn(res)
        // }
      }
    })

    if (sharedVars.spinnerTimer) clearTimeout(sharedVars.spinnerTimer)
    if (args.duration) {
      sharedVars.spinnerTimer = setTimeout(() => {
        gif.setStore({ spinnerDetails: { visible: false } })
        if (args.callbackFn) args.callbackFn()
      }, args.duration)
    }


  }
}
/* ============================================================ */
export function convertNumToMKNumber(num: number) {
  const fnNum = num.toString()
  if (!fnNum || fnNum == '0') return '$0'
  if (fnNum.length == 7) return `$${fnNum[0]}.${fnNum[1]}M`
  if (fnNum.length == 6) return `$${fnNum[0]}.${fnNum[1]}K`
  return `$${fnNum}`
}
