import AsyncStorage from '@react-native-community/async-storage'
/* =========================== */
export function clearStorage(): Promise<boolean> {
    return new Promise(async resolve => {
        AsyncStorage.clear(e => {
            if (e) {
                throw e
            } else {
                return resolve(true)
            }
        })
    })
}
/* =========================== */
export function setStorage(key, argToSet): Promise<boolean> {
    return new Promise(resolve => {
        AsyncStorage.setItem(key, JSON.stringify(argToSet), e => {
            if (e) {
                throw e
            } else {
                return resolve(true)
            }
        })
    })
}
/* =========================== */
export function getStorage(key: string): Promise<any> {
    return new Promise(async resolve => {
        await AsyncStorage.getItem(key, (e, r) => {
            if (e) {
                throw e
            } else {
                return resolve(JSON.parse(r))
            }
        })
    })
}
