export function convertImportedFileToAWebWorker(worker: any) {
  const code = worker.toString()
  const blob = new Blob(['(' + code + ')()'])
  return URL.createObjectURL(blob)
}
/* =========================== */
export function hardObjectCopy(objOrArr: any) {
  /* todo: make this works with Float32Array */
  /* todo:this might go very deep in the object because this fn is recursive ,prevent it by limiting it */
  if (!objOrArr) {
    return objOrArr
  }
  // let variable
  const newObjOrArr = Array.isArray(objOrArr) ? [] : {}
  for (const key in objOrArr) {
    const variable = objOrArr[key]
    newObjOrArr[key] = (typeof variable === 'object') ? hardObjectCopy(variable) : variable
  }

  return newObjOrArr
}
/* =========================== */
export function makeArray(length: number, initializeValue: any | { min: number, max: number } = null) {
  return Array.from({ length }, () => {
    if (typeof initializeValue == 'object') {
      return random(initializeValue.min, initializeValue.max)
    } else {
      return initializeValue
    }
  })
}
/* =========================== */
/* ============================================ */
export function random(min = undefined, max = undefined) {
  const rand = Math.random()
  if (typeof min === 'undefined') {
    return rand
  } else if (typeof max === 'undefined') {
    if (min instanceof Array) {
      return min[Math.floor(rand * min.length)]
    } else {
      return rand * min
    }
  } else {
    if (min > max) {
      const tmp = min
      min = max
      max = tmp
    }

    return rand * (max - min) + min
  }
}
/* =========================== */
export async function delay(timeMs = 1000) {
  return new Promise(resolve => setTimeout(() => { resolve(1) }, timeMs))
}
