export async function getDaysEventsOfJalali() {

  const allEvents = []
  let daysLengthInMonth = 0
  for (let month = 1; month <= 12; month++) {
    if (month <= 6) {
      daysLengthInMonth = 31
    } else {
      daysLengthInMonth = 30
    }
    for (let day = 1; day <= daysLengthInMonth; day++) {
      const res = await (await global.fetch(`https://farsicalendar.com/api/sh/${day}/${month}`)).json()
      /* api/sh,wc,ic/11,1,27/3,6,9 */
      if (!res || res.type != 'success') throw 'error_in_event_fetch'
      allEvents.push(res.values)
    }
  }
  return allEvents
}
