export function gregorian_to_jalali(gy, gm, gd) {
  gy = Math.trunc(gy)
  gm = Math.trunc(gm)
  gd = Math.trunc(gd)
  const g_d_m = [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334]
  let jy, days
  if (gy > 1600) {
    jy = 979
    gy -= 1600
  } else {
    jy = 0
    gy -= 621
  }
  const gy2 = (gm > 2) ? (gy + 1) : gy
  days = (365 * gy) + (Math.trunc((gy2 + 3) / 4)) - (Math.trunc((gy2 + 99) / 100)) + (Math.trunc((gy2 + 399) / 400)) - 80 + gd + g_d_m[gm - 1]
  jy += 33 * (Math.trunc(days / 12053))
  days %= 12053
  jy += 4 * (Math.trunc(days / 1461))
  days %= 1461
  if (days > 365) {
    jy += Math.trunc((days - 1) / 365)
    days = (days - 1) % 365
  }
  const jm = (days < 186) ? 1 + Math.trunc(days / 31) : 7 + Math.trunc((days - 186) / 30)
  const jd = 1 + ((days < 186) ? (days % 31) : ((days - 186) % 30))
  return [jy, jm, jd]
}
/* =========================== */
export function jalali_to_gregorian(jy, jm, jd) {
  return new Promise(resolve => {
    jy = Math.trunc(jy)
    jm = Math.trunc(jm)
    jd = Math.trunc(jd)
    let gy, days, gd, gm
    if (jy > 979) {
      gy = 1600
      jy -= 979
    } else {
      gy = 621
    }
    days = (365 * jy) + ((Math.trunc(jy / 33)) * 8) + (Math.trunc(((jy % 33) + 3) / 4)) + 78 + jd + ((jm < 7) ? (jm - 1) * 31 : ((jm - 7) * 30) + 186)
    gy += 400 * (Math.trunc(days / 146097))
    days %= 146097
    if (days > 36524) {
      gy += 100 * (Math.trunc(--days / 36524))
      days %= 36524
      if (days >= 365) days++
    }
    gy += 4 * (Math.trunc(days / 1461))
    days %= 1461
    if (days > 365) {
      gy += Math.trunc((days - 1) / 365)
      days = (days - 1) % 365
    }
    gd = days + 1
    const sal_a = [0, 31, ((gy % 4 == 0 && gy % 100 != 0) || (gy % 400 == 0)) ? 29 : 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    for (gm = 0; gm < 13; gm++) {
      const v = sal_a[gm]
      if (gd <= v) break
      gd -= v
    }
    return resolve([gy, gm, gd])
  })
}
