import * as types from './types'
import { ScaledSize } from 'react-native'
/* just variables which will be used by components here (plus split view(V) from controllers(C) */
/* ------------ */
/**
 * note: this will be converted to a plain object(the way redux likes it!) 
 *  but now i used class to combine types and objects(line) 
 * Note: dont forget ?: 
 */
class reduxInitialState_ {
  /* ------------------------------- */
  /* setting */
  setting_muteAudio?: boolean = null
  setting_darkTheme?: boolean = null
  setting_notifications?: boolean = true
  /* ------------------------------- */
  /* note:dont put these toghether because in that case when ur using setStore all items must be set */
  /* ------------------------------- */
  confirmedUsersPerDay?: { x: number, y: number }[] = []
  confirmedSalesPerDay?: { x: number, y: number }[] = []
  // asdasd?=false
  windowSize?: ScaledSize = null 
  totalConfirmedPersonalSaleCounter?: number = 0

  activateTopMenu?: boolean = null 
  popupDetail?: {
    visible: boolean,
    title?: string,
    icon?: string,
    content?: string,
    type?: 'alert' | 'confirm',
    withCheckBox?: {
      defaultValue: boolean,
      text: string,
    },
    // callbackFn?: Function
  } = null
  toastDetails?: {
    visible: boolean,
    title?: string,
    content?: string,
    type?: 'alert' | 'confirm',
    // callbackFn?: Function
  } = null
  spinnerDetails?: {
    visible: boolean,
    title?: string,
    content?: string,
    type?: 'alert' | 'confirm',
    // callbackFn?: Function
  } = null
  /* ----------------------------- */
}
/* ----------------------------- */
const reduxInitialState = new reduxInitialState_()
/* ----------------------------- */
export default reduxInitialState
/* ----------------------------- */


