const assetDir_a = Platform.OS == 'web' ? '.' : 'file:///android_asset'
let defaultMarketerImg = ''
let defaultBgImg = ''
if (Platform.OS == 'web' && development) {
    defaultMarketerImg = host + ':3000/assets/' + defaultMarketerImgPlain
    defaultBgImg = host + ':3000/' + defaultBgPlain
} else if (Platform.OS == 'web' && !development) {
    defaultMarketerImg = host + '/assets/' + defaultMarketerImgPlain
    defaultBgImg = host + '/' + defaultBgPlain
} else if (Platform.OS == 'android') {
    defaultMarketerImg = assetDir_a + '/assets/' + defaultMarketerImgPlain
    defaultBgImg = assetDir_a + '/' + defaultBgPlain
}

const imageComponentPlatformQuirk = Platform.OS == 'web' ? 'assets/' : 'asset:/'
export {
    assetDir_a,
    defaultMarketerImg,
    defaultBgImg,
    defaultMarketerImgPlain,
    imageComponentPlatformQuirk,
    defaultBgPlain,
}
