export function handleBackPress() {
  BackHandler.addEventListener('hardwareBackPress', () => {
    try { /* using try catch since it is still possible to have err on some devices(and back btn not worth an app crash) */


    /* ---------- */
    /* prevent back btn functionality while spinner is active */
    if (getStore('spinnerDetails')?.visible) {
      return true
    }
    /* ---------- */
    /* manage paths */
      const c_r: types.routes = getRouteName().split('/')[1] as any
    // alert(JSON.stringify([c_r,]))
    const ignoreBackBtn: types.routes[] = [
      'Dashboard',
      '',
      'Init',
    ]
      console.log(getRouteName())
    if (ignoreBackBtn.includes(c_r)) {
      // if (c_r != '/AdminDashboard' && c_r != '/EsSlider' && c_r != '/' && c_r != '/Init') {
      //     return false
      // } else {
      // return true
      // alert(JSON.stringify(['cant go back',]))
      myApp.showPupop({
        title: 'exit',
        content: 'exit the app?',
        type: 'confirm',
        callbackFn: (e) => {
          if (e) {
            try {
              BackHandler.exitApp()
            } catch (error) {
              console.log(error)
            }
          }
        }
      })
    } else {
      goBack()
    }
    return true
    } catch (error) {
      console.log(error)
      return true
    }
  })
}
